@import url('https://fonts.googleapis.com/css?family=Dancing+Script&display=swap');

.missionStatementDesktop{
    top: 75px;
    right: 155px;
    text-align:left;
    font-size: 1rem;
    line-height: .5rem;
    z-index: 500
}

.tinyAddress{
    font-size: 18px;
    font-weight: 700;
    margin-bottom: 1rem;
    margin-top: .5rem;
    cursor: pointer;
    color: #14A399;
}

.backgroundStyle {
    overflow-x: hidden;
    position:fixed;
   
    bottom:0;
    /* right:0; */
    left:0;
    bottom: 0;
    width: 100%;
    background-image: url(/video.mp4);
    background-repeat: no-repeat;
    background-position: bottom left;
    animation-name: backgroundIn;
    animation-duration:2s;
}

.userReviewDesktop {
    position:absolute;
    bottom:20%;
    left: 3%;
    width:55%;
    text-shadow: 1px 1px 1px black; 
    border-radius: 15px;
    z-index: 5;
    font-family: 'Nunito', sans-serif;
    text-align: left;
    color: white;
    font-size: 24px;
    font-style: italic;
    opacity: 0;
    animation: container 2s .75s forwards;
}

.quoteGrid{
    margin-top: 1rem;
    font-size: 18px;
    display: grid;
    grid-row-gap: 1.5rem;
    grid-column-gap: .5rem;
    grid-template-columns: auto auto;
    line-height: 36px;
    overflow: hidden;
}

@keyframes container {
    0%{
        opacity:0;
        transform: scale(.75)
    }
    100%{
        opacity:1;
        transform: scale(1)
    }
}

@keyframes backgroundIn{
    0%{
        opacity:0;
        transform: scale(1.25)
    }
    100%{
        opacity:1;
        transform: scale(1)
    }
}

.indent{
    text-indent: 15px;
    display: inline-block;
}

.indent img {
    height:20px;
    width:auto;
}

.userReviewDesktop .indent> a{
    text-decoration: none;
    color:white;
}

.indent  a {
    position: relative;
    color: white;
    text-decoration: none;
}

.missionStatement{
    margin-top: 1.25rem;
    margin-left: 2rem;
    margin-right: 2rem;
    font-size: 1rem;
    font-style: italic;
    line-height: 1.5rem;
}

.indent  a:hover {
    color: white;
}

.indent  a:before {
    content: "";
    position: absolute;
    width: 100%;
    height: 2px;
    bottom: 0;
    left: 0;
    background-color: white;
    visibility: hidden;
    -webkit-transform: scaleX(0);
    transform: scaleX(0);
    -webkit-transition: all 0.3s ease-in-out 0s;
    transition: all 0.3s ease-in-out 0s;
  }

  .indent  a:hover:before {
    visibility: visible;
    -webkit-transform: scaleX(1);
    transform: scaleX(1);
  }

  /* media queries */


  @media screen and (min-width: 1099px){

    .userReviewDesktop{
        font-size: 30px;
    }
    .quoteGrid{
        font-size: 24px;
        line-height: 44px;
    }
  }

  

  @media screen and (max-width: 750px){

    .userReviewDesktop {
        /* bottom: 35%; */
        font-size: 22px;
    }
    .quoteGrid{
        font-size: 16px;
        line-height: 30px;
    }
  }

  @media screen and (max-height: 500px){

    .userReviewDesktop {
        bottom: 20%;
        font-size: 18px;
        /* width: 40% */
        
    }
    .quoteGrid{
        display:none;
    }
  }


    @media screen and (min-width: 620px) and (max-width: 450px) {

        .mobile{
            display:none;
        }
    
        .notMobile{
            display:inline;
        }
        
    }
